import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DashboardComponent } from './dashboard.component';
import { MaterialModule } from '@angular/material';

import { RouterModule } from '@angular/router';

import { DashRoutes } from './dashboard.routes';
import { DashRoutes as routes} from './dashboard.routes';
import { DashOrdersComponent } from './dash-orders/dash-orders.component';
import { DashProductsComponent } from './dash-products/dash-products.component';
import { DashUserComponent } from './dash-user/dash-user.component';
import { DashCropAccountComponent } from './dash-crop-account/dash-crop-account.component';
import { DashCampaignComponent } from './dash-campaign/dash-campaign.component';
import { DashCouponComponent } from './dash-coupon/dash-coupon.component';
import { DashReportComponent } from './dash-report/dash-report.component';
import { DashConfigurationComponent } from './dash-configuration/dash-configuration.component';
import { ProductService } from '../core/services/product.service';

@NgModule({
  imports: [
    CommonModule,
    MaterialModule,
    RouterModule.forChild(routes)
  ],
  declarations: [
    DashboardComponent,
    DashOrdersComponent,
    DashProductsComponent,
    DashUserComponent,
    DashCropAccountComponent,
    DashCampaignComponent,
    DashCouponComponent,
    DashReportComponent,
    DashConfigurationComponent
  ],
  providers: [
    ProductService
  ]
})
export class DashboardModule { }



